window.addEventListener('load', () => {

  if (!document.querySelector('#map')) {
    return false
  } else {

    ymaps.ready(init);

    function init(){
      const mapElement = document.getElementById("map");
      const mapOptions = {
        zoom: 15,
      };

      // Получаем текущую ширину экрана
      const screenWidth = window.innerWidth;

      if (screenWidth <= 1024) {
        mapOptions.center = [52.225261, 104.111481];
      } else {
        mapOptions.center = [52.226083, 104.096502];
      }

      const map = new ymaps.Map(mapElement, mapOptions);

      const placemark = new ymaps.Placemark(
        [52.225261, 104.111481],
        {
          hintContent: 'улица Ивана Кочубея, 56Б',
          balloonContent: 'улица Ивана Кочубея, 56Б'
        },
        {
          preset: 'islands#icon', // Стиль метки
          iconColor: '#D0043C' // Цвет метки
        }
      );

      map.geoObjects.add(placemark);
    }

  }
})

