window.addEventListener('load', () => {
  const header = document.querySelector('.header')
  const upBtn = document.querySelector('.up-btn')
  const mobileBtn = document.querySelector('.header__mobile-btn')
  const menu = document.querySelector('.header__menu')

  let top = window.scrollY

  if (top !== 0 ) {
    header.classList.add('sticky')
  }

  window.addEventListener('scroll', () => {
    top = window.scrollY

    if (top > 0) {
      header.classList.add('sticky')
    } else {
      header.classList.remove('sticky')
    }
  })

  upBtn.addEventListener('click', () => {
    window.scrollTo(0, 0)
  })

  function toggleMobileMenu() {
    header.classList.toggle('mobile-menu')
    mobileBtn.classList.toggle('active')
  }

  mobileBtn.addEventListener('click', toggleMobileMenu)
  menu.addEventListener('click', (e) => {
    if (e.target.closest('.header__menu-item')) {
      document.querySelectorAll('.header__menu-item').forEach(el => el.classList.remove('active'))
      e.target.closest('.header__menu-item').classList.add('active')
      toggleMobileMenu()
    }
  })
})
