function initInputs(inputs) {
  function focus() {
    this.parentElement.querySelector('label').classList.add('active')

    if (this.parentElement.querySelector('input')) {
      this.parentElement.querySelector('input').classList.add('active')
    } else if (this.parentElement.querySelector('textarea')) {
      this.parentElement.querySelector('textarea').classList.add('active')
    } else {
      return null
    }
  }

  function blur() {
    if (!this.value.trim()) {
      this.parentElement.querySelector('label').classList.remove('active')

      if (this.parentElement.querySelector('input')) {
        this.parentElement.querySelector('input').classList.remove('active')
      } else if (this.parentElement.querySelector('textarea')) {
        this.parentElement.querySelector('textarea').classList.remove('active')
      } else {
        return null
      }

    }
  }

  inputs.forEach(el => {

    if (el.querySelector('input')) {
      const input = el.querySelector('input')

      input.addEventListener('focus', focus)
      input.addEventListener('blur', blur)
    } else if (el.querySelector('textarea')) {
        const textarea = el.querySelector('textarea')

        textarea.addEventListener('focus', focus)
        textarea.addEventListener('blur', blur)
    } else {
      return null
    }
  })
}

window.addEventListener('load', () => {


  if (!document.querySelector('.input')) {
    return null
  } else {

    initInputs(document.querySelectorAll('.input'))

    const requiredInputs = document.querySelectorAll('.input input[required], .input textarea[required]');

// Создайте объект для отслеживания, было ли уже событие blur на каждом поле
    const blurState = {};

// Переберите каждый обязательный инпут и добавьте обработчик события для проверки валидации
    requiredInputs.forEach(input => {
      blurState[input.id] = false; // Установите изначальное значение в false

      input.addEventListener('blur', () => {
        if (!blurState[input.id]) {
          blurState[input.id] = true;
          validateInput(input);
        }
      });
    });

// Функция для проверки валидации и вывода сообщения об ошибке
    function validateInput(input) {
      const showError = input.required && !input.value;
      const inputContainer = input.closest('.input');
      const errorSpan = inputContainer.querySelector('.error');

      if (showError) {
        inputContainer.classList.add('error');
        input.classList.add('error');
        if (errorSpan) {
          errorSpan.innerText = 'Это поле обязательно для заполнения';
        } else {
          const newErrorSpan = document.createElement('span');
          newErrorSpan.classList.add('error');
          newErrorSpan.innerText = 'Это поле обязательно для заполнения';
          inputContainer.appendChild(newErrorSpan);
        }
      } else {
        inputContainer.classList.remove('error');
        input.classList.remove('error');
        if (errorSpan) {
          errorSpan.remove();
        }
      }
    }
  }
});
