import Swiper from "swiper";
import { Navigation, Pagination } from "swiper/modules";

window.addEventListener('load', () => {

  if (!document.querySelector('.one-news')) {
    return false;
  } else {
    const mainSlider = new Swiper('.one-news-slider .swiper', {
      modules: [Navigation, Pagination],
      loop: true,
      pagination: {
        el: '.swiper-pagination',
      },
      navigation: {
        nextEl: '.swiper-button-next',
        prevEl: '.swiper-button-prev',
      }
    });

    const slidesItems = document.querySelectorAll('.one-news-main-slides-item');

    slidesItems.forEach((item, index) => {
      item.addEventListener('click', () => {
        mainSlider.slideTo(index); // Перелистываем слайдер к нужному индексу
        setActiveSlide(index); // Выделяем выбранную картинку
      });
    });

    function setActiveSlide(index) {
      slidesItems.forEach((item, i) => {
        if (i === index) {
          item.classList.add('active'); // Добавляем класс для подсветки выбранной картинки
        } else {
          item.classList.remove('active'); // Убираем класс подсветки с остальных картинок
        }
      });
    }
  }
});
